<template>
  <div id="app">
      <b-navbar toggleable="md" type="dark" class="nav-background">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand href="/">Mr. Stock</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">

        <b-navbar-nav class="ml-auto">
          <!---->
          <b-navbar-nav>
            <b-nav-item href="/"></b-nav-item>
            <b-nav-item v-if="!loggedIn" href="/login"></b-nav-item>
            <b-nav-item v-if="!loggedIn" href="/signup"></b-nav-item>
            <b-nav-item v-if="loggedIn" href="/logout"></b-nav-item>
          </b-navbar-nav>

          <!---->
          <b-nav-item-dropdown right>
            <template slot="button-content">
              <em></em>
            </template>
            <b-dropdown-item href="/main/profile"></b-dropdown-item>
            <b-dropdown-item href="/main/userlist"> </b-dropdown-item>
            <b-dropdown-item href="/main/settings"></b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      
      </b-collapse>
    </b-navbar>
    <b-button @click="loggedIn"></b-button>
    <router-view/>
  </div>
</template>

<script>
export default {
  computed: {
    loggedIn() {
      return this.$store.getters.loggedIn
    }
  }
};
</script>

<style>
body {
  background-color: lightgray;
}
.nav-background {
  background-color: black;
}
</style>



